/* WorkshopItem Component Styles */

.workshopItemCard {
  height: 100%;
  display: flex;
  flex-direction: column;
  transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  cursor: pointer;
}

.workshopItemCard:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.12);
}

.workshopItemTitle {
  font-size: 16px !important;
  font-weight: bold !important;
  color: #1976d2;
  line-height: 1.3;
}

.workshopItemDate {
  font-size: 14px !important;
  color: #666 !important;
  font-weight: 500;
  margin-top: 4px;
}

.workshopItemLocation {
  font-size: 13px !important;
  color: #888 !important;
  font-weight: 400;
  margin-top: 2px;
  display: flex;
  align-items: center;
}

.imageContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 16px;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  overflow: hidden;
}

.workshopItemImage {
  width: 100%;
  max-width: 280px;
  height: 180px;
  object-fit: cover;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
}

.workshopItemImage:hover {
  transform: scale(1.02);
}

.workshopItemContent {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 16px;
}

.workshopItemDescription {
  font-size: 14px !important;
  text-align: justify;
  line-height: 1.5;
  color: #333;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  line-clamp: 4;
  -webkit-box-orient: vertical;
}

/* Responsive Design */
@media (max-width: 768px) {
  .workshopItemTitle {
    font-size: 15px !important;
  }
  
  .workshopItemDate {
    font-size: 13px !important;
  }
  
  .workshopItemLocation {
    font-size: 12px !important;
  }
  
  .workshopItemImage {
    height: 160px;
  }
  
  .workshopItemDescription {
    font-size: 13px !important;
    -webkit-line-clamp: 3;
    line-clamp: 3;
  }
}

@media (max-width: 480px) {
  .workshopItemContent {
    padding: 12px;
  }
  
  .workshopItemTitle {
    font-size: 14px !important;
  }
  
  .workshopItemDate {
    font-size: 12px !important;
  }
  
  .workshopItemLocation {
    font-size: 11px !important;
  }
  
  .imageContainer {
    padding: 12px;
  }
  
  .workshopItemImage {
    height: 140px;
  }
  
  .workshopItemDescription {
    font-size: 12px !important;
    -webkit-line-clamp: 3;
    line-clamp: 3;
  }
}
